// Font size

@for $i from 1 through 10 {
  .fs-#{$i}rem {
    font-size: #{$i}rem;
  }
}

@for $i from 1 through 10 {
  .fs-#{$i}em {
    font-size: #{$i}em;
  }
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6, {
  display: block;
  margin-bottom: 0.5rem;
  font-family: inherit;
  font-weight: 700;
  line-height: 1.2;
}

h1, .h1 {
  font-size: 2.5rem;
}

h2, .h2 {
  font-size: 2rem;
}

h3, .h3 {
  font-size: 1.75rem;
}

h4, .h4 {
  font-size: 1.5rem;
}

h5, .h5 {
  font-size: 1.25rem;
}

h6, .h6 {
  font-size: 1rem;
}

// Font weight

@for $i from 1 through 7 {
  .fw-#{$i*100} {
    font-weight: #{$i*100};
  }
}

// Text align

.text-center {
  text-align: center;
}